@import './colors';
@import './mixins';
@import './variables';

.hc-login-container {
    @include fontSize(15px);
    font-family: 'Open sans';
    background-image: url("data:image/jpeg;base64,");
    background-repeat: no-repeat;
    background-position: center top;
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;

    a {
        color: $blue;
        text-decoration: none;
        outline: none;
        border-radius: 4px;

        &:focus {
            outline: 2px solid $blue;
            outline-offset: 2px;
        }
    }
}

.hc-login {
    fieldset {
        border: 0;
    }

    label {
        @include fontSize(14px);
        color: $gray-500;
        font-weight: normal;
        margin-top: 20px;
    }

    .login-input {
        width: 100%;
        height: 40px;
        padding: 0px 10px;
        margin-bottom: 20px;
        margin-top: 5px;
        box-sizing: border-box;
        border: 1px solid $slate-gray-300;
        border-image: none;
        border-style: solid;
        font-family: inherit;
        @include fontSize(15px);
        line-height: inherit;
        outline: none;

        &:hover {
            border: 1px solid $blue;
            background-color: tint($blue, 95%);
        }

        &:focus {
            border: 1px solid $blue;
            box-shadow: 0 0 3px $blue;
        }
    }

    .hc-form-field {
        display: block;
    }
    width: 360px;
    padding: 30px 40px;
    box-shadow: 0 0 13px $shadow;
    border: 1px solid $slate-gray-200;
    border-radius: 5px;
    margin-bottom: 20px;
    background: $white;

    .privacy {
        line-height: 1rem;
        color: $slate-gray-400;
        padding: 0 30px;
        text-align: center;
        @include fontSize(12px);
    }

    .new-to-hcat {
        padding: 10px 0;
        margin: 10px -40px -30px -40px;
        width: calc(100% + 80px);
        padding: 18px 0;
        border-top: 1px solid $slate-gray-200;
        text-align: center;
        @include fontSize(14px);
    }

    .forgot-password {
        float: right;
        @include fontSize(11px);
        margin-top: 3px;
    }

    .logo {
        width: 240px;
        height: 40px;
        margin: 10px auto 35px;
        padding: 0;
        background-size: contain;
    background-repeat: no-repeat;
    background-position-x: center;
    background-image: url("data:image/svg+xml;base64,");
    }

    .new-account {
        margin-left: 10px;
        display: inline-block;
        font-weight: 600;
    }

    .login-button {
        width: 100%;
        height: 40px;
        font-family: inherit;
        @include fontSize(15px);
        line-height: inherit;
        background-color: $green;
        font-weight: 600;
        color: $white;
        border-radius: 4px;
        border: none;
        margin-top: 8px;
        margin-bottom: 30px;
        cursor: pointer;
        outline: none;

        &:hover {
            background-color: darken($green, 10%);
        }
        &:active {
            background-color: darken($green, 20%);
        }

        &:focus {
            background-color: darken($green, 10%);
            box-shadow: 0 0 0 3px transparentize($green, 0.6);
        }
    }
}

.hc-login-footer {
    @include fontSize(12px);
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 360px;
    line-height: 1.2;
    color: $gray-400;

    a {
        text-decoration: none;
        color: $blue;
        display: inline;
        padding: 0;
    }

    .hc-site-btn {
        background-color: $gray-200;
        border-radius: 4px;
        padding: 7px 10px;
        color: $gray-400;
        cursor: pointer;

        &:hover {
            background-color: darken($gray-200, 10%);
            color: $gray-600;
        }
    }
}

@media screen and (max-width: 370px) {
    .hc-login-footer, .hc-login {
        width: 95%;
    }

    .hc-login {
        padding: 30px 30px;

        .logo {
            width: 90%;
        }

        .new-account {
            display: block;
            margin: 5px 0 0 0;
        }
    }

    .hc-login-footer {
        flex-direction: column;
        text-align: center;

        .hc-site-btn {
            margin-top: 10px;
        }
    }
}

